
import React from 'react';
import styled from 'styled-components';
import { Avatar, Text as Txt , Header} from '.'
import { mobileWidth,  gradient, disabledTxtColor } from '../config';
import { useLocation } from "react-router";
import logo from '../assets/parkstash_logo.svg';
import { Popover, Menu, Badge } from 'antd';
import Grid from './grid';
import { logout } from '../store/actions/user';
import { useDispatch } from 'react-redux';
import { moment } from '../utils/time'
const TopNavBar = () => {
  const dispatch = useDispatch();
  const location = useLocation().pathname;
  return (
    <MiniNav>
      <Text className="capitalize bold txt-md txt-left" style={{ minWidth: "100px", width: "100%" }}>
        {location === "/" || location === "/overview" ? "Overview" : location.replace("/", "")}
      </Text>
    
      <Popover
        placement="bottomRight"
        content={
          <div style={{ zIndex: 1000, maxWidth: '300px'}}>
            <Grid customCols='1fr auto' placeItems='start stretch'>
              <Header bold padding='0 0 .5em 0' textAlign='left'>Abnormality</Header>
              <i className='fas fa-bell txt-md'></i>
            </Grid>
            {abnormalities.map(({ garage, date }, i) => {
              const style = i < (abnormalities.length - 1)? { 
                paddingBottom: '1em',  
                marginBottom: '.5em', 
                borderBottom: "1px solid "+ disabledTxtColor
              } : {};
            return  (      
                <Grid customCols='auto 1fr' placeItems='start stretch' style={style}>
                  <i className='fas fa-car txt-md' style={{placeSelf: "center"}}></i>
                  <Header bold textAlign='left' size={4}>{garage}</Header>
                  <Txt style={{gridColumn: "1/-1"}} textAlign='right' >
                    Abnormality Detected at 50% Occupancy.
                  </Txt>
                  <Txt style={{gridColumn: "1/-1"}} color={disabledTxtColor} textAlign='right'>
                    {date.format('hh:mm A')}
                  </Txt>
                </Grid>
             )})}
          </div>
        }
        trigger="hover"
      >
        <Badge count={3}>
          <i className='fa fa-bell txt-shadow pointer txt-lg'></i>
        </Badge>
      </Popover>

      <Popover
        placement="bottomRight"
        content={
          <Menu style={{ zIndex: 1000 }}>
            <Menu.Item key="0" className="noselect">
              <Grid customCols='auto 1fr' onClick={() => dispatch(logout())}>
                <i className='fas fa-sign-in-alt txt-md'></i>
                <span>Logout</span>
              </Grid>
            </Menu.Item>
          </Menu>
        }
        trigger="hover"
      >
        <div>
          <Avatar className='shadow' style={{ background: "white", borderRadius: "50%", margin: "0 1em" }} url={logo} />
        </div>
      </Popover>
    </MiniNav>)
}

const abnormalities = [{
  garage: "North 10th Street Garage",
  date: moment(),
}, {
  garage: "North 10th Street Garage",
  date: moment(),
},
{
  garage: "North 10th Street Garage",
  date: moment(),
}]


const Text = styled.div`
    padding-left: 9.5em;
    @media(max-width: ${mobileWidth}){
       padding-left: 0;
       font-size: 1.2em;
    } 
  `;

const MiniNav = styled.div`
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  position: fixed;
  font-size: 15px;
  top: 0;
  right : 0;
  left: 0;
  padding: 0 1.5em 0 0;
  z-index: 9;
  width: 100vw;
  height: 4em; 
  background: ${gradient};  
  color: white;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: 1fr auto auto;
  grid-template-rows: 1fr;
  place-items: center end;
    @media(max-width: ${mobileWidth}){
      top : inherit;
      padding-left: 0
      grid-gap: .5em;
    } `;

export default TopNavBar;