import React  from 'react'
import { Group } from '@vx/group';
import { TimeSeries } from '../../model/domain/metrics';
import * as axis from './axis';
import { DrawArea } from './draw-area';
import { useSize } from './hooks/size';
import { useScale } from './hooks/scale';
import { useStackedData, useLinearData } from './hooks/data';
import { Charts } from '.';

export interface LineChartProps {
  height: number;
  width: number;
  timeSeriesResult: TimeSeries.Result;
  metricDescriptor: TimeSeries.MetricDescriptor;
  backgroundColor: string;
  setSelected: any;
  isSelected: any;
  noneSelected: any;
  chart?: any;
  setChart?: any;
}

const axisProps = (tickFormat: Function, align: boolean, textAnchor: string) => ({
  hideAxisLine: true,
  tickStroke: 'gray',
  tickLength: 6,
  tickFormat,
  tickLabelProps: () => ({
    fontWeight: 'bold',
    textAnchor: textAnchor,
    fill: 'gray', 
    fontSize: 12,
    ...(align && {
      dy: 5,
      dx: -2
    })
  })
})

export function Chart(props: LineChartProps | any) {
  const { height, width, chart, setChart, timeSeriesResult, metricDescriptor, backgroundColor, isSelected, noneSelected, setSelected} = props;
  const rects = useSize({height, width});
  const [ linear, stacked ] = [ useLinearData(timeSeriesResult), useStackedData(timeSeriesResult)]
  const { max, points } =  chart === Charts.multiLine? linear : stacked;

  const { scale, tickFormat } = useScale({
    height : rects.drawArea.height,
    width : rects.drawArea.width,
    timeSeriesResult,
    chart,
    yMax: max,
    points
  });

  const axisBottom = axis.createXAxis(scale.x as any, axisProps(tickFormat.x, false, 'middle'));
  const axisLeft = axis.createYAxis(scale.y, axisProps(tickFormat.y, true, 'end'));
  return (
    <div style={{height: height + 'px', width: '100%', position: 'relative', backgroundColor}}>
        <svg
          style={{position: 'absolute', top: rects.yAxis.top, left: rects.yAxis.left}}
          width={rects.yAxis.width}
          height={rects.yAxis.height}>
            <g transform={`0, 0)`}>
              <Group left={rects.yAxis.width}>
                {axisLeft}
              </Group>
            </g>
        </svg>
        <svg
          style={{position: 'absolute', top: rects.xAxis.top, left: rects.xAxis.left}}
          width={rects.xAxis.width}
          height={rects.xAxis.height}>
            <g transform={`translate(${rects.xAxis.padding}, 0)`}>
              <Group left={0}>
                {axisBottom}
              </Group>
            </g>
        </svg>
        
        <div style={{position: 'absolute', top: rects.drawArea.top, left: rects.drawArea.left}}>
            <DrawArea
              chart={chart}
              setChart={setChart}
              backgroundColor={backgroundColor}
              width={rects.drawArea.width}
              height={rects.drawArea.height}
              timeSeriesResult={timeSeriesResult}
              metricDescriptor={metricDescriptor}
              xScale={scale.x as any}
              yScale={scale.y} 
              isSelected={isSelected}
              noneSelected={noneSelected}
              setSelected={setSelected}
          />
        </div>
    </div>
  );
}