import React, { useState, useEffect } from "react";
import { Garage } from "../../../model/domain/garage";
import { Card, Header, Text, Grid, Badge } from "../../../style";
import { Progress } from "antd";
import { primaryColor, disabledTxtColor } from "../../../config";
import { moment } from '../../../utils/time';

const GarageCard = (props: Garage & any) => {
  const { name, address, maxOccupancy, active, edit, occupancyUpdateSource, occupancy, getTableById, occupancyTableId, imageUrl, abnormality} = props;
  const [table, setTable] = useState("");
  const occupancyPercent = Number((((occupancy?.count || 0) / maxOccupancy) * 100).toFixed(2));

  useEffect(() => {
    if (occupancyTableId)
      getTableById(occupancyTableId)
        .then((res: any) => {
          if (res?.success)
            setTable(res?.garageOccupancyTable?.title || "");
        })
  }, [])
  return (
    <Card shadow style={{maxWidth: "80vw", width: '800px'}}>
      <Grid customCols='auto 1fr' placeItems='start stretch'>
        <Grid placeSelf='stretch' style={{borderRight: '2px solid ' + disabledTxtColor, paddingRight: '1em'}}>
        {imageUrl && imageUrl[0]? 
        <img src={imageUrl[0]} style={{maxWidth: "8em"}}/> :
          <i style={{fontSize: '9em', color: disabledTxtColor}} className='fas fa-parking'></i>
        }
        </Grid>
        <Grid placeItems='stretch'>
          <Grid gridGap='0' placeItems='stretch'>
            <Header textAlign="left">
              <Grid customCols="auto auto 1fr auto" placeSelf="end" gridGap='0'>
                <Text bold>{name} &nbsp;</Text>
                <span className="noselect">
                  <i className="fa fa-edit pointer hoverr txt-sm" onClick={edit}></i> &nbsp;
              <i className="fa fa-trash pointer txt-sm" style={{ color: "gray" }}></i> &nbsp;
            </span>
                <div></div>
                <Badge
                  style={{ fontSize: ".65em" }}
                  disabled={!active}
                >
                  {active ? "Active" : "Disabled"}
                </Badge>

              </Grid>
            </Header>

            <Text color="gray" textAlign="left">
              <i className="far fa-compass" ></i> {address}
            </Text>
            {
              table &&
              <Text color="gray" textAlign="left">
                <i className="fa fa-table" ></i> {table}
              </Text>
            }
          </Grid>

          <Grid placeSelf="stretch" cols="2" placeItems="start" gridGap=".5em">
            <Progress
              strokeWidth={15}
              successPercent={0}
              strokeColor={primaryColor}
              percent={occupancyPercent}
              format={() => <div className='bold'>{occupancyPercent}%</div>}
              style={{
                gridColumn: '1/-1',
                width: "100%",
                placeSelf: "center stretch",
                paddingRight: '.5em'
              }}
            />
            <Badge
              inverted={occupancy?.count}
              disabled={!occupancy?.count}
              clasName={"grid"}
            >
                <Text bold color='inherit'>
                  {occupancy?.count || "N/A"} 
                  <span style={{ fontSize: "1.3em" }}> / </span>
                  {maxOccupancy}
                </Text>
            </Badge>

     
            <Badge disabled={!abnormality?.active}  style={{placeSelf :'center end'}}>
                <i className='fa fa-bell'></i>&nbsp;
                Abnormality{abnormality?.active? `: ${abnormality.percent * 100}%` : ''}
            </Badge>

          </Grid>
          <Grid cols='2'>
            <Text placeSelf="center start" color={disabledTxtColor}>
              &nbsp;{occupancyUpdateSource === 'integration' ? "Automatic" : "Manual"}
            </Text>
            {
              occupancy?.updatedAt &&
              <div style={{ color: "grey", fontSize: ".75em", placeSelf: "center end" }}>
                Last Updated: {moment(occupancy.updatedAt).toString().substring(4, 24)}
              </div>
            }
          </Grid>
        </Grid>
      </Grid>
    </Card>
  );
};

export default GarageCard;
