import React, { CSSProperties, useRef, useEffect, useState } from "react";
import { Card, Header, Grid, Empty as EmptyEl, Loader, Button, Notification } from "../../style";
import { primaryColor } from "../../config";
import { LineChart as ILineChart } from "../../model/domain/dashboard";
import { Chart } from "./chart";
import { overflow } from "../../utils/text";
import LegendTable from "./legend-table";

export enum Charts {
  stackedArea = "stackedArea",
  multiLine = "multiLine",
  stackedBar = "stackedBar",
  combined = "combined"
}

interface AdditionalProps {
  span?: number;
  filter?: boolean | any;
  filterWidth?: number;
  metricDataStatus?: any;
  className?: string;
  legend?: boolean;
  isPercent?: boolean;
  types?: string[]
}

const isWhite = (color: string) => (color === 'white' || color.toLowerCase() === '#ffffff');
const containerHeight = 400;
export default (props: ILineChart & AdditionalProps) => {
  const { span, filter, metricDataStatus, legend, filterWidth, backgroundColor, title, isPercent, types = [] } = props;
  const color = isWhite(backgroundColor) ? primaryColor : '#ffffff';
  const [containerWidth, setContainerWidth] = useState(300);
  const [selected, setSelected] = useState(-1);
  const isSelected = (index: number) => selected === index;
  const container = useRef<any>(null);
  const resize = () => setContainerWidth(container?.current?.clientWidth);

  const [chartType, setChartType] = useState<Charts>(types[0] as Charts || Charts.stackedBar);

  useEffect(() => {
    if (isPercent)
      setChartType(Charts.multiLine)
  }, [isPercent]);

  useEffect(resize, [container?.current?.clientWidth, filterWidth]);
  useEffect(() => {
    window?.addEventListener("resize", resize);
    return () => window.removeEventListener("resize", resize);
  }, []);

  return (
    <>
      <Card
        placeSelf="stretch"
        shadow
        style={{ gridColumn: 'span ' + span, maxWidth: "100%" }}
        grid
        customCols={"1fr auto"}
        gridGap={"0 1em"}
        className="slide-up noselect"
      >
        <Header
          className="grid"
          color={color}
          style={{
            gridColumn: "1/-1",
            width: "100%",
            gridTemplateColumns: "1fr auto auto",
            placeItems: "start ",
          }}
          bold
          size={4}
        >
          <Grid mobileLayout={false} customCols="auto 1fr auto" gridGap=".5em" padding=".2em .5em"
            width={'100%'}
            style={{
              placeItems: 'start',
              borderRadius: "1em",
              textTransform: "capitalize",
              placeSelf: "start",
            }}
          >
            <Header bold size={3.75} textAlign='left'>{overflow(title, 25)}</Header>
            <div style={{ placeSelf: "end" }}>
              <Button
                invert
                minWidth="3em"
                margin='0 1em'
                noBorder={chartType !== Charts.multiLine}
                onClick={() => Notification.success({ title: 'Success', message: 'Exporting Data to Excel' })}
                faIcon={'fas fa-file-download'}
                tooltip='Download the current data on Excel'
              />
              {types.includes(Charts.multiLine) && <Button
                invert={chartType !== Charts.multiLine}
                minWidth="3em"
                noBorder={chartType !== Charts.multiLine}
                onClick={() => setChartType(Charts.multiLine)}
                faIcon={'fas fa-chart-line'}
                tooltip={`Multi Line`}
              />}

              {types.includes(Charts.stackedArea) &&
                <Button
                  invert={chartType !== Charts.stackedArea}
                  minWidth="3em"
                  disabled={isPercent}
                  noBorder={chartType !== Charts.stackedArea}
                  onClick={() => setChartType(Charts.stackedArea)}
                  faIcon={'fas fa-chart-area'}
                />}

              {types.includes(Charts.stackedBar) &&
                <Button
                  invert={chartType !== Charts.stackedBar}
                  minWidth="3em"
                  disabled={isPercent}
                  noBorder={chartType !== Charts.stackedBar}
                  onClick={() => setChartType(Charts.stackedBar)}
                  faIcon={'fas fa-chart-bar'}
                />}
            </div>
            {filter ? filter : null}
          </Grid>
        </Header>
        <div style={{ width: "100%", marginTop: "1em" }} ref={container}>
          {(containerWidth < 100)
            ? <Empty height={containerHeight} />
            : metricDataStatus?.loading
              ? <Loading height={containerHeight} />
              : metricDataStatus?.error
                ? <Error height={containerHeight} />
                : !metricDataStatus?.data!?.timeSeriesResult
                  ? <DataMissing height={containerHeight} />
                  : <Chart
                      noneSelected={selected === -1}
                      chart={chartType}
                      setChart={setChartType}
                      isSelected={isSelected}
                      setSelected={setSelected}
                      backgroundColor={backgroundColor}
                      height={containerHeight}
                      width={containerWidth}
                      timeSeriesResult={metricDataStatus.data!.timeSeriesResult}
                      metricDescriptor={metricDataStatus.data!.metricDescriptor}
                  />
          }
        </div>
      </Card>
      <LegendTable
        show={legend}
        timeSeries={metricDataStatus?.data?.timeSeriesResult?.timeSeries}
      />
    </>
  );
};

const d100: CSSProperties = { height: '100%', width: '100%', textAlign: 'center', lineHeight: '100px' };

const Empty = ({ height }: any) => <EmptyEl text="Screen too Small" style={{ height }} />;

const Loading = ({ height }: any) => (
  <div className="grid" style={{ ...d100, height }}>
    <div>
      <Loader.Spinner />
      <Header bold>Loading..</Header>
    </div>
  </div>
);

const Error = ({ height }: any) => <div style={{ ...d100, height }}><EmptyEl text="Error Occured" style={{ marginTop: "1em" }} /></div>

const DataMissing = ({ height }: any) => <div style={{ ...d100, height }}><EmptyEl text="No Data Found" style={{ marginTop: "1em" }} /></div>