import { defaultInterval, yearToDate, getInterval,  currentMonth } from "../../utils/time";

const json = {
   "timeframes": [
      {
         "label": "Custom",
         "enteries": [
            {
               "value": null,
               "label": "Custom"
            }
         ]
      },
      {
         "label": "To-Date",
         "enteries": [
            {
               "value": currentMonth(),
               "label": "Current Month",
            },
            {
               "value": currentMonth(-1),
               "label": "Last Month"
            },
            {
               "value": yearToDate(),
               "label": "Year To Date"
            },
         ]
      },
      {
         "label": "Interval",
         "enteries": [
            {
               "value": getInterval(30),
               "label": "Last 30 Days"
            },
            {
               "value": getInterval(90),
               "label": "Last 90 Days"
            }
         ]
      }
   ],
   "aggregations": {
      "label": "Aggregations",
      "values": [
         {
            "value": null,
            "label": "Auto"
         },
        
         {
            "label": "Daily",
            "value": {
               "alignmentReducer": "MEAN",
               "alignmentPeriod": 1440
            }
         },
         {
            "label": "Weekly",
            "value": {
               "alignmentReducer": "MEAN",
               "alignmentPeriod": 60 * 24 * 7
            }
         },
         {
            "label": "Monthly",
            "value": {
               "alignmentReducer": "MEAN",
               "alignmentPeriod": 60 * 24 * 30
            }
         }
      ]}
}

export default json;