import React, { useState, useEffect } from 'react';
import Layout from '../../layout';
import ScoreCards from '../../components/score-card/score-cards';
import { useWidgets } from '../../hooks/widget';
import { useSelector } from 'react-redux';
import { Grid, Button } from '../../style';
import Filter from '../garage/analytics/filter';
import { useMetricData } from '../../hooks/metrics';
import useTimeseries, { defaultTimeSeries as ts } from '../../hooks/timeseries';
import options from './filter-options';
import chart from './chart.json';
import Chart from '../../components/chart';
import { currentMonth } from '../../utils/time';
import { Select } from 'antd';

const defaultTimeSeries = { ...ts, interval: currentMonth(), resourceIds: ["5f1147320b05ea57e9d4e27c"] };

const Citations = () => {
    const eId = useSelector((state: any) => state.user.enterprise.id);
    const { loading, scoreCards } = useWidgets({ eId, screen: "citation" });
    const [open, setOpen] = useState(true);
    const shouldPad = window.innerWidth > 1360;
    const filterWidth = open ? 375 : 0;
    const [toggle, setToggle] = useState(false)
    const { metricDataStatus, refresh } = useMetricData({
        timeseries: defaultTimeSeries,
        interval: currentMonth(),
        eId
    });

    const modifiedData = metricDataStatus?.data ? {
        ...metricDataStatus?.data,
        timeSeriesResult: {
            ...metricDataStatus?.data.timeSeriesResult,
            timeSeries: [
                {
                    ...metricDataStatus?.data.timeSeriesResult?.timeSeries[0],
                    displayLabel: "Citation Revenue: $500. <br>Citation Number"
                }
            ]

        }
    } : null;
    const modifiedMetricData = {
        ...metricDataStatus,
        data: modifiedData
    }

    const { timeseries, actions } = useTimeseries(defaultTimeSeries as any);
    const filteredOptions = { ...options, aggregations: undefined }
    const props = {
        defaultValue: timeseries,
        metricDataStatus: modifiedMetricData,
        filterWidth,
        loading: metricDataStatus.loading,
        submit: () => refresh(timeseries),
        reset: () => refresh(timeseries),
        actions,
        hideTimeSelection: true,
        timeseries,
        options: filteredOptions
    }
    useEffect(() => {
        if (toggle) {
            refresh(timeseries);
            setToggle(false)
        }
    }, [timeseries])
    const selectProps = { style: { width: '100px', marginRight: "1em" }, disabled: metricDataStatus.loading };
    return (
        <Layout sidebar>
            <Grid customCols="1fr auto" width="100%" gridGap="0" placeItems="start" style={{ height: "100%" }}>
                <div style={{ width: "100%", placeSelf: 'start end' }}>
                    <Layout.Top style={shouldPad ? { margin: '0 0 0 8%', maxWidth: '100%' } : {}}
                    >
                        <div></div>
                        <ScoreCards
                            loading={loading}
                            eId={eId}
                            scoreCards={scoreCards}
                        />

                    </Layout.Top>
                    <Layout.Bottom style={shouldPad ?
                        { margin: '-10em 0 0 8%', maxWidth: '100%' } :
                        { margin: '-10em auto auto auto', maxWidth: '1500px' }}
                    >  <Grid placeItems="start stretch" padding="1em" height="100%">
                            <Grid cols="1" gridGap="0 1em">
                                <Chart
                                    className='slide-up'
                                    filterWidth={filterWidth}
                                    metricDataStatus={modifiedMetricData}
                                    {...chart}
                                    filter={
                                        <div>
                                            <Select
                                                onChange={(e: any) => {
                                                    actions.setAlignment(JSON.parse(e));
                                                    setToggle(true);
                                                }}
                                                defaultValue={"Auto"}
                                                {...selectProps}
                                            >
                                                {options.aggregations.values.map(({ value, label }: any) => <Select.Option value={JSON.stringify(value)}>
                                                    {label}
                                                </Select.Option>)}
                                            </Select>
                                            {!open ?
                                                <Button
                                                    placeSelf="end"
                                                    rounded
                                                    shadow
                                                    minWidth="3em"
                                                    onClick={() => setOpen(!open)}
                                                    faIcon={`fa fa-filter`}
                                                /> : null}</div>}
                                />
                                <Filter {...props} open={open} toggle={() => setOpen(false)} />
                            </Grid>
                        </Grid>
                    </Layout.Bottom>
                </div>
                <div className='overlay-tablet' style={{ width: filterWidth }}></div>
            </Grid>
        </Layout>
    );
}

export default Citations;