export const useSize = ({height, width}: { height: number; width: number; }) =>{
    const yAxisRect = { left: 0, top: 0, height: height, width: 35 };
    const p = 0;
  
    const xAxisRect = {
      left: yAxisRect.width - 30,
      top: height - 30,
      height: 30,
      width: width - yAxisRect.width,
      padding: 30/*TODO: investigate more and comment and make it more general*/
    };
  
    const drawAreaRect = {
      left: yAxisRect.width - p,
      top: 0,
      height: height - xAxisRect.height,
      width: width - yAxisRect.width - p
    }
  
    return {
      yAxis: yAxisRect,
      xAxis: xAxisRect,
      drawArea: drawAreaRect
    };
  }