import { defaultInterval, yearToDate, getInterval } from "../../../utils/time";

const json = {
   "timeframes": [
      {
         "label": "Custom",
         "enteries": [
            {
               "value": null,
               "label": "Custom"
            }
         ]
      },
      {
         "label": "To-Date",
         "enteries": [
            {
               "value": defaultInterval(),
               "label": "Today"
            },
            {
               "value": yearToDate(),
               "label": "Year To Date"
            },
         ]
      },
      {
         "label": "Interval",
         "enteries": [
            {
               "value": getInterval(7),
               "label": "Last 1 Week"
            },
            {
               "value": getInterval(30),
               "label": "Last 1 Month"
            },
            {
               "value": getInterval(90),
               "label": "Last 3 Months"
            },
            {
               "value": getInterval(365),
               "label": "Last 1 Year"
            },
         ]
      }
   ],
   "metrics": [
      {
         "value": "garage_occupancy_count",
         "label": "Garage Occupancy Count"
      },
      {
         "value": "garage_occupancy_percent",
         "label": "Garage Occupancy Percent"
      }
   ],
   "aggregations":  {
      "label": "Max/Min Occupancy Selection",
      "values": [
      {
         "value": null,
         "label": "None"
      },
      {
         "label": "Hourly Max",
         "value": {
            "alignmentReducer": "MAX",
            "alignmentPeriod": 60
         }
      },
      {
         "label": "Daily Max",
         "value": {
            "alignmentReducer": "MAX",
            "alignmentPeriod": 1440
         }
      },
      {
         "label": "Hourly Min",
         "value": {
            "alignmentReducer": "MIN",
            "alignmentPeriod": 60
         }
      },
      {
         "label": "Daily Min",
         "value": {
            "alignmentReducer": "MIN",
            "alignmentPeriod": 1440
         }
      }
   ]}
}

export default json;