import React, { useState } from 'react'
import { Grid, List, Text, Button } from '../../style';
import { ScoreCard } from './score-card';
import { invertedTxtColor } from '../../config';
import { moment } from '../../utils/time';

const ScoreCards = ({ style, scoreCards, loading }: any) => {
  const [ refresh, setRefresh ] = useState(false)
  return (
    <div className="slide-up"
      style={{ width: "100%", gridColumn: "1/-1", padding: ".9em .5em", ...style }}>
      {!loading && <Text
        style={{ gridColumn: '1/-1', fontSize: '.8em' }}
        textAlign='right'
        color={invertedTxtColor}
      >
       &nbsp; Updated on: {moment().format('hh:mm A')}
      </Text>}
      <Grid cols={loading ? 1 : 5} mobileCols='1fr 1fr' placeItems={loading ? 'center' : 'stretch'} style={{ minHeight: "9em" }}>
        <List
          list={scoreCards}
          loading={loading}
          map={(item: any, index: number) => (
            <ScoreCard.FlatValue
              borderRight={index < scoreCards.length - 1}
              name={item.title}
              refresh={refresh}
              flatValueQuery={item.flatValueQuery}
            />
          )}
        />
      </Grid>

    </div>);
}

export default ScoreCards;